.question {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
color: #333;
display: contents;
font-size: 2rem;
}

.choice {
cursor: pointer;
width: 20rem;
height: 2.2rem;
font-size: 15px;
border: 1px solid #3E8BD5;
border-radius: 20px;
}

.choice:hover {
    background-color: #18214A;
    color: white;
}

.selectedChoice {
    background-color: #18214A;
    color: white;
}

.questionBox {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;

}
.questionBox > * {
    margin-top: 0.55rem;
}







